
<template>
    <div id="pageName">
        <h2>리뷰작성 -개인-</h2>
    </div>
    <hr class="my-4" />
    <!-- 가게 점수주기 -->
    <div id="starRating" class="btn-group" role="group" aria-label="Basic radio toggle button group">
        <input type="radio" class="btn-check" name="btnradio" id="btnradio1" autocomplete="off" @click="selectRating(rating[0].score)">
        <label class="btn btn-outline-primary" for="btnradio1">1</label>

        <input type="radio" class="btn-check" name="btnradio" id="btnradio2" autocomplete="off" @click="selectRating(rating[1].score)">
        <label class="btn btn-outline-primary" for="btnradio2">2</label>

        <input type="radio" class="btn-check" name="btnradio" id="btnradio3" autocomplete="off" @click="selectRating(rating[2].score)">
        <label class="btn btn-outline-primary" for="btnradio3">3</label>

        <input type="radio" class="btn-check" name="btnradio" id="btnradio4" autocomplete="off" @click="selectRating(rating[3].score)">
        <label class="btn btn-outline-primary" for="btnradio4">4</label>

        <input type="radio" class="btn-check" name="btnradio" id="btnradio5" autocomplete="off" @click="selectRating(rating[4].score)">
        <label class="btn btn-outline-primary" for="btnradio5">5</label>
    </div> 
    <h2>내 점수: {{ myRating }}</h2>
    <hr class="my-4" />
    <!-- 리뷰 사진 첨부 -->
    <div id="reviewImg" class="custom-file">
        사진추가 : -백엔드로 사진값전송할 코드 짜야함-
        <input id="customFile" type="file" @change="readInputFile"/>
    </div>
    <br>
    <!-- 이미지 미리보기 -->
    <div id="imagePreview">
        <img id="img" class="rounded mx-auto d-block"/>
    </div>
    <!-- 리뷰 입력란 -->
    <div id="reviewWrite" class="form-floating">
        <textarea class="form-control" placeholder="리뷰를 입력하세요" id="floatingTextarea2" style="height: 300px"></textarea>
        <label for="floatingTextarea2">리뷰 입력</label>
    </div>
    <!-- 버튼 -->
    <div>
        <button id="writeComplete" type="submit" class="btn btn-primary">리뷰작성완료</button>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                rating: [
                    {score: 1},
                    {score: 2},
                    {score: 3},
                    {score: 4},
                    {score: 5},   
                ],
                myRating: ""
            }
        },
        methods: {
            selectRating(score) {
                this.myRating = score;
            }
        }
    }
</script>

